<template>
    <div id="ReceitPage">
        <!-- PRINT START -->
        <section class="hero is-medium is-light is-bold">
        <vue-easy-print tableShow ref="easyPrint" >
                <div class="hero-head">
                    <div class="container has-text-centered">
                        <h1 class="title">
                            Nicolas Clinic
                        </h1>
                        <h2 class="subtitle">
                            <p class="is-size-5">Aguinaldo Street, Bais City</p>
                            <p class="is-size-7">Clinic Hours: MWF 9AM - 5PM</p>
                            <p class="is-size-7">No Clinic: Thursdays and Sundays</p>
                        </h2>
                    </div>
                </div>
                <div class="hero-body">
                    <div class="columns">
                        <div class="column">Patient: <u> {{ firstname }} {{ middlename }} {{ lastname }} </u></div>
                        <div class="column is-two-fourth">Age: <u> {{ age }} </u></div>
                    </div>
                    <div class="columns">
                        <div class="column">Address: <u> {{ address }} </u> </div>
                        <div class="column is-two-fifths">Sex: <u> {{ sex }} </u> </div>
                    </div>
                    <div class="columns">
                        <div class="column"></div>
                        <div class="column is-two-fifths">Date: <u> {{ issueDate }} </u> </div>
                    </div>
                    <div class="columns">
                        <div class="column is-two-fifths">
                            <img src="../assets/rx.jpg" alt="" class="image is-128x128">
                        </div>
                        <div class="column">
                            {{ prescription }}
                        </div>
                    </div>
                    <div class="columns is-centered">
                        <div class="has-text-centered">Dr. Darian Nicolas</div>
                    </div>
                    <div class="columns is-centered">
                        <div class="has-text-centered">Lic. No: 120195</div>
                    </div>
                </div>

        </vue-easy-print>
        <div class="column is-2">
            <button class="button is-success is-rounded" @click="print">Print</button>
        </div>
        </section>


    </div>
</template>
<script>
import db from './firebaseInit'
import vueEasyPrint from 'vue-easy-print'

export default {
    name: 'ReceitPage',
    components: {
        vueEasyPrint,
    },
    data() {
        return {
            userProfileId: this.$route.params.userProfileId,
            userHistoryId: this.$route.params.userHistoryId,

            // data need
            firstname: null,
            middlename: null,
            lastname: null,
            dateOfBirth: null,
            age: null,
            sex: null,
            address: null,

            issueDate: null,
            prescription: null
        }
    },
    methods: {
        print () {
            this.$refs.easyPrint.print()
        },
    },
    beforeRouteEnter (to, from, next) {
        // user/patient profile
        db.collection('users').where('uid_user', '==', to.params.userProfileId).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(userProf => {
                    userProf.id = doc.id,
                    userProf.name = doc.data().name,
                    userProf.uid_user = doc.data().uid_user
                })
            })
        })
        // patient history
        db.collection('history').where('usersId', '==', to.params.userHistoryId).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(userHist => {
                    userHist.id = doc.id,
                    userHist.date = doc.data().date
                    userHist.prescription = doc.data().prescription
                })
            })
        })   
    },
    created() {
        // get patient profile == name and dob only
        db.collection('users').where('uid_user', '==', this.$route.params.userProfileId).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                this.firstname = doc.data().firstname,
                this.middlename = doc.data().middlename,
                this.lastname = doc.data().lastname,
                this.dateOfBirth = doc.data().dateOfBirth,
                this.sex = doc.data().sex,
                this.age = doc.data().age,
                this.address = doc.data().address
            })
        })
        
        // get patient history record == date and prescription
        db.collection('history').doc(this.$route.params.userHistoryId).get().then(doc => {
            this.issueDate = doc.data().date
            this.prescription = doc.data().prescription
        })

    }
}
</script>
<style>
@media print {
    button .button {
        display: none;
    }
    figure, img {
        height: 128px;
        width: 128px;
        display: inherit;
    }

    html {
        margin-left: 10%;
        margin-right: 10%;
    }

}

.hero-body {
    margin: 0% 10% 0 15%;
}

.pad {
    margin-left: 20%;
}
</style>

